/* =========================================
   1. VARIABLES & THEME
   ========================================= */
:root {
  /* Shadcn/ui Dark Theme inspired (Zinc palette) */
  --background: #09090b;
  --foreground: #fafafa;
  --card: #09090b;
  --card-foreground: #fafafa;
  --popover: #09090b;
  --popover-foreground: #fafafa;
  --primary: #fafafa;
  --primary-foreground: #18181b;
  --secondary: #27272a;
  --secondary-foreground: #fafafa;
  --muted: #27272a;
  --muted-foreground: #a1a1aa;
  --accent: #27272a;
  --accent-foreground: #fafafa;
  --destructive: #7f1d1d;
  --destructive-foreground: #fafafa;
  --border: #27272a;
  --input: #27272a;
  --ring: #d4d4d8;
  --radius: 0.5rem;

  /* Fonts */
  --font-sans: 'Inter', ui-sans-serif, system-ui, -apple-system, sans-serif;
  --font-mono: 'JetBrains Mono', 'Menlo', 'Monaco', 'Courier New', monospace;

  /* Syntax Highlighting - Base Colors */
  --token-comment: #71717a;
  --token-code: #a1a1aa;
  --token-keyword: #e879f9;
  --token-def: #4ade80;
  --token-number: #2dd4bf;
  --token-string: #fcd34d;

  /* Grammar Specific Tokens (Dark Mode Defaults) */
  --token-ingredient: #c4b5fd;
  --token-cookware: #fde047;
  --token-timer: #67e8f9;
  --token-temp: #fda4af;
  --token-unit: #f472b6;
  --token-qty: #5eead4;
  --token-syntax: #71717a;
  --token-decl: #10b981;

  /* Semantic Status Colors */
  --status-success-bg: transparent;
  --status-success-text: #4ade80;
  --status-success-border: rgba(74, 222, 128, 0.2);
  --status-error-bg: transparent;
  --status-error-text: #f87171;
  --status-error-border: rgba(248, 113, 113, 0.2);

  /* Preview Item Colors */
  --item-ingredient-text: #8b5cf6;
  --item-ingredient-bg: rgba(139, 92, 246, 0.1);
  --item-ingredient-border: rgba(139, 92, 246, 0.2);
  --item-cookware-text: #f59e0b;
  --item-cookware-bg: rgba(245, 158, 11, 0.1);
  --item-cookware-border: rgba(245, 158, 11, 0.2);
  --item-timer-text: #06b6d4;
  --item-timer-bg: rgba(6, 182, 212, 0.1);
  --item-timer-border: rgba(6, 182, 212, 0.2);
  --item-temp-text: #f43f5e;
  --item-temp-bg: rgba(244, 63, 94, 0.1);
  --item-temp-border: rgba(244, 63, 94, 0.2);
  --item-ref-text: #10b981;
  --item-ref-bg: rgba(16, 185, 129, 0.1);
  --item-ref-border: rgba(16, 185, 129, 0.2);
}

.light {
  --background: #ffffff;
  --foreground: #09090b;
  --card: #ffffff;
  --card-foreground: #09090b;
  --popover: #ffffff;
  --popover-foreground: #09090b;
  --primary: #18181b;
  --primary-foreground: #fafafa;
  --secondary: #f4f4f5;
  --secondary-foreground: #18181b;
  --muted: #f4f4f5;
  --muted-foreground: #71717a;
  --accent: #f4f4f5;
  --accent-foreground: #18181b;
  --destructive: #ef4444;
  --destructive-foreground: #fafafa;
  --border: #e4e4e7;
  --input: #e4e4e7;
  --ring: #18181b;

  /* Syntax Highlighting - Light Mode Overrides */
  --token-code: hsl(240 5.3% 26.1%);
  --token-comment: #52525b;
  --token-keyword: #a21caf;
  --token-def: #15803d;
  --token-number: #0f766e;
  --token-string: #b45309;

  /* Grammar Tokens (Light) */
  --token-ingredient: #7c3aed;
  --token-cookware: #d97706;
  --token-timer: #0891b2;
  --token-temp: #e11d48;
  --token-unit: #db2777;
  --token-qty: #0d9488;
  --token-syntax: #a1a1aa;

  /* Status Colors (Light) */
  --status-success-bg: #dcfce7;
  --status-success-text: #15803d;
  --status-success-border: #bbf7d0;
  --status-error-bg: #fee2e2;
  --status-error-text: #b91c1c;
  --status-error-border: #fecaca;
}

/* =========================================
   2. GLOBAL RESET & BASE
   ========================================= */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
  height: 100vh;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: var(--border);
  border-radius: 99px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: var(--muted-foreground);
}

/* =========================================
   3. LAYOUT & STRUCTURE
   ========================================= */
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

header {
  margin-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

header h1 {
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: -0.025em;
  color: var(--foreground);
}

header p {
  color: var(--muted-foreground);
  font-size: 0.875rem;
}

.playground {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
  flex: 1;
  min-height: 0;
}

/* =========================================
   4. UI COMPONENTS
   ========================================= */
/* Panels / Cards */
.editor-section, .result-section {
  background-color: var(--card);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.header-bar {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid var(--border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  min-height: 56px;
}

h2 {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--muted-foreground);
  letter-spacing: -0.01em;
}

/* Buttons */
.tab-btn {
  background: transparent;
  border: none;
  color: var(--muted-foreground);
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.2s;
  font-family: var(--font-sans);
}
.tab-btn:hover {
  color: var(--foreground);
  background-color: rgba(255, 255, 255, 0.05);
}
.tab-btn.active {
  background-color: var(--card);
  color: var(--foreground);
  box-shadow: 0 1px 2px rgba(0,0,0,0.1);
}
.light .tab-btn:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

#theme-toggle:hover {
    background-color: var(--secondary);
}

/* Status Badge */
.status {
  font-size: 0.75rem;
  padding: 0.125rem 0.625rem;
  border-radius: 9999px;
  font-weight: 500;
  border: 1px solid transparent;
}
.status.success {
  background-color: var(--status-success-bg);
  color: var(--status-success-text);
  border-color: var(--status-success-border);
}
.status.error {
  background-color: var(--status-error-bg);
  color: var(--status-error-text);
  border-color: var(--status-error-border);
}

/* =========================================
   5. EDITOR AREA
   ========================================= */
.editor-container {
  position: relative;
  flex: 1;
  display: flex;
  overflow: hidden;
  background-color: #0c0c0e;
}
.light .editor-container {
  background-color: #fafafa;
}

.editor-container textarea,
.editor-container .highlights {
  font-family: var(--font-mono);
  font-size: 0.875rem;
  line-height: 1.7;
  padding: 1rem;
  width: 100%;
  height: 100%;
  border: none;
  background: transparent;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

textarea {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  color: transparent;
  caret-color: var(--foreground);
  resize: none;
  outline: none;
  overflow: auto;
}

.highlights {
  color: var(--foreground);
  overflow: hidden;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}


/* =========================================
   6. CODE VIEWER & OUTPUT
   ========================================= */
.code-viewer {
  display: flex;
  flex: 1;
  overflow: auto;
  background-color: #0c0c0e;
  position: relative;
}
.light .code-viewer {
  background-color: #fafafa;
}

/* Highlight JS Line Numbers */
.hljs-ln {
    border-collapse: collapse;
}
.hljs-ln td {
    padding: 0;
    border: 0;
}

.hljs-ln-line[data-line-number="1"] {
  padding-block-start: 1rem;
}

.hljs-ln-numbers {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    text-align: right;
    color: var(--token-comment);
    border-right: 1px solid var(--border) !important;
    vertical-align: top;
    padding-right: 0.75rem !important;
    min-width: 3rem;
}
.hljs-ln-code {
    padding-left: 1rem !important;
}

pre {
  flex: 1;
  padding-inline: 0.5rem;
  margin: 0;
  background-color: inherit; /* Matches parent */
  font-size: 0.875rem;
  line-height: 1.678;
  white-space: pre-wrap;
  overflow-wrap: break-word;
}

code {
  font-family: var(--font-mono);
  color: var(--token-code);
}

/* =========================================
   7. PREVIEW OUPUT
   ========================================= */
.preview-output {
  flex: 1;
  padding: 2rem;
  overflow: auto;
  font-family: var(--font-sans);
  line-height: 1.7;
  color: var(--foreground);
  background-color: var(--card);
  font-size: 15px;
}
.preview-output:has(.json-tree) {
  padding: 0;
}

/* Headings in Preview */
.preview-output h1 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  border-bottom: 2px solid var(--border);
  padding-bottom: 0.5rem;
}
.preview-output h2 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: var(--foreground);
}
.preview-output h3 {
  font-size: 1.15rem;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--foreground);
}
.preview-output h4 {
  margin-bottom: 0.5rem;
}

/* Lists in Preview */
.preview-output ul, .preview-output ol {
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}
.preview-output li {
  margin-bottom: 0.25rem;
}
.preview-output .steps li {
  margin-bottom: 0.5rem;
}

/* Semantic Items (Badge Styles) */
.preview-output .ingredient {
  font-weight: 600;
  color: var(--item-ingredient-text);
  background: var(--item-ingredient-bg);
  padding: 0.1em 0.3em;
  border-radius: 4px;
  border: 1px solid var(--item-ingredient-border);
}
.preview-output span.cookware {
  font-weight: 600;
  color: var(--item-cookware-text);
  background: var(--item-cookware-bg);
  padding: 0.1em 0.3em;
  border-radius: 4px;
  border: 1px solid var(--item-cookware-border);
}
.preview-output .timer {
  font-weight: 600;
  color: var(--item-timer-text);
  background: var(--item-timer-bg);
  padding: 0.1em 0.3em;
  border-radius: 4px;
  border: 1px solid var(--item-timer-border);
}
.preview-output .timer.async {
  border-style: dashed;
  opacity: 0.9;
}
.preview-output .timer.async::after {
  content: " ⏳";
  font-size: 0.8em;
  opacity: 0.7;
}
.preview-output .temp {
  font-weight: 600;
  color: var(--item-temp-text);
  background: var(--item-temp-bg);
  padding: 0.1em 0.3em;
  border-radius: 4px;
  border: 1px solid var(--item-temp-border);
}
.preview-output .reference {
  font-weight: 600;
  color: var(--item-ref-text);
  background: var(--item-ref-bg);
  padding: 0.1em 0.3em;
  border-radius: 4px;
  border: 1px solid var(--item-ref-border);
  cursor: default;
}
.preview-output .action {
  display: inline-block;
  padding: 0.1em 0.4em;
  border-radius: 4px;
  background: var(--secondary);
  color: var(--foreground);
  font-size: 0.75em;
  font-weight: 600;
  text-transform: uppercase;
  margin-right: 0.5rem;
  vertical-align: middle;
  letter-spacing: 0.05em;
}

/* Metadata & Sections */
/* Metadata & Sections */
.preview-output .recipe-meta {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.preview-output .meta-item {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.preview-output .meta-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.preview-output .meta-icon {
  font-size: 1.5rem;
  line-height: 1;
}

.preview-output .meta-content {
  display: flex;
  flex-direction: column;
}

.preview-output .meta-label {
  font-size: 0.7em;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--muted-foreground);
  font-weight: 600;
}

.preview-output .meta-value {
  font-size: 1.1em;
  font-weight: 700;
  color: var(--foreground);
}

.preview-output .metadata {
  background: var(--secondary);
  padding: 1rem;
  border-radius: var(--radius);
  margin-bottom: 2rem;
  border: 1px solid var(--border);
}

.preview-output .metadata ul{
  margin-block-end:0;
}
.preview-output .shopping-list, 
.preview-output div.cookware {
  background-color: var(--secondary);
  padding: 0.5rem 1rem 0.25rem;
  border-radius: var(--radius);
  margin-bottom: 1.5rem;
  border: 1px solid var(--border);
}

.preview-output .instructions section {
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border);
}
.preview-output .instructions section:last-child {
  border-bottom: none;
}

.preview-output .quantity {
  color: var(--foreground);
  font-weight: 500;
  opacity: 0.9;
}
.preview-output .unit {
  color: var(--muted-foreground);
  font-size: 0.9em;
}
.preview-output .optional {
  font-style: italic;
  color: var(--muted-foreground);
  font-size: 0.85em;
  margin-left: 0.25em;
}


.preview-output .mass-badge {
  display: inline-block;
  font-size: 0.75em;
  color: var(--muted-foreground);
  background-color: var(--secondary);
  border: 1px solid var(--border);
  border-radius: 99px;
  padding: 0 0.5em;
  margin-left: 0.5em;
  vertical-align: middle;
  cursor: help;
  line-height: 1.4;
}

.preview-output .gross-mass {
  font-size: 0.75em;
  color: var(--warning); 
  /* Assuming warning color exists, otherwise using muted but italic */
  font-style: italic;
  margin-left: 0.5em;
}

.separator {
  list-style: none;
  border-block-end: 1px dotted var(--muted-foreground);
}

/* =========================================
   8. JSON TREE VIEW
   ========================================= */
.json-tree {
  font-family: var(--font-mono);
  font-size: 0.875rem;
  line-height: 1.5;
  padding: 1rem;
  background-color: #0c0c0e;
  color: var(--foreground);
}

.light .json-tree {
  background-color: #fafafa;
}

/* Controls */
.json-controls {
  padding: 0.5rem;
  border-bottom: 1px solid var(--border);
  display: flex;
  gap: 0.5rem;
}
.tree-btn {
  background-color: var(--secondary);
  color: var(--secondary-foreground);
  border: 1px solid var(--border);
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  border-radius: 4px;
  cursor: pointer;
  font-family: var(--font-sans);
  transition: background-color 0.2s;
}
.tree-btn:hover {
  background-color: var(--muted);
  color: var(--foreground);
}

/* Node Layout */
.json-node { margin-left: 0.5rem; }
.json-tree > .json-node { margin-left: 0; }

.json-header {
  cursor: pointer;
  user-select: none;
  display: inline-block;
  width: 100%;
}
.json-header:hover {
  background-color: rgba(255, 255, 255, 0.05);
}
.light .json-header:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.json-toggle {
  display: inline-block;
  width: 1rem;
  text-align: center;
  color: var(--muted-foreground);
  font-size: 0.75rem;
  margin-right: 0.25rem;
  transition: color 0.2s;
}
.json-header:hover .json-toggle {
  color: var(--foreground);
}

/* Collapsed State */
.json-node.collapsed > .json-children,
.json-node.collapsed > .json-footer {
  display: none;
}

.json-children {
  margin-left: 0.5rem;
  border-left: 1px solid var(--border);
  padding-left: 0.5rem;
}
.json-footer {
  padding-left: 1.25rem;
}
.json-item, .json-prop {
  margin: 0;
}

/* JSON Keywords */
.json-key { color: var(--token-ingredient); }
.json-str { color: var(--token-string); }
.json-num { color: var(--token-number); }
.json-bool { color: var(--token-keyword); }
.json-null { color: var(--token-comment); font-style: italic; }
.json-bracket, .json-brace, .json-comma { color: var(--token-syntax); }
.json-count {
  display: inline-block;
  padding: 0 0.4em;
  margin-left: 0.5em;
  font-size: 0.75em;
  background-color: var(--border);
  color: var(--muted-foreground);
  border-radius: 4px;
  vertical-align: middle;
  opacity: 0.7;
}
.json-node.collapsed .json-count {
  opacity: 1;
}


/* =========================================
   9. SYNTAX HIGHLIGHTING (TOKENS)
   ========================================= */
.token-comment { color: var(--token-comment); }
.token-header { color: var(--foreground); font-weight: 600; }
.token-frontmatter { color: var(--token-comment); }

.token-ingredient { color: var(--token-ingredient); }
.token-cookware { color: var(--token-cookware); }
.token-timer { color: var(--token-timer); }
.token-temp { color: var(--token-temp); }
.token-decl { color: var(--token-decl); }
.token-qty { color: var(--token-qty); }
.token-syntax { color: var(--token-syntax); }

/* Highlight.js Overrides */
.hljs {
  background: transparent;
  color: var(--foreground);
}
.hljs-comment { color: var(--token-comment); }
.hljs-keyword, .hljs-selector-tag, .hljs-title, .hljs-section, .hljs-doctag, .hljs-name, .hljs-strong {
  font-weight: bold;
}
.hljs-emphasis { font-style: italic; }
.hljs-string, .hljs-title, .hljs-section, .hljs-built_in, .hljs-literal, .hljs-type, .hljs-addition, .hljs-tag, .hljs-quote, .hljs-name, .hljs-selector-id, .hljs-selector-class {
  color: var(--token-string);
}
.hljs-attr { color: var(--token-keyword); }
.hljs-number { color: var(--token-number); }
.hljs-literal { color: var(--token-def); }
.hljs-symbol, .hljs-bullet { color: var(--token-keyword); }

/* Nutrition Panel */
.nutrition-panel {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.nutrition-panel .nut-header {
  font-weight: 500;
  margin-bottom: 0.5rem;
  font-size: 0.9em;
  color: var(--muted-foreground);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.nutrition-panel .nut-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
  text-align: center;
}

.nutrition-panel .nut-item {
  background: var(--secondary);
  padding: 0.5rem;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nutrition-panel .nut-item small {
  font-size: 0.7em;
  opacity: 0.7;
}

.nutrition-panel .nut-item .label {
  font-size: 0.7em;
  text-transform: uppercase;
  color: var(--muted-foreground);
  margin-bottom: 2px;
}

.nutrition-panel .est-badge {
    background: var(--primary);
    color: var(--primary-foreground);
    border-radius: 9px;
    padding: 0 6px;
    font-size: 0.7em;
    font-weight: normal;
}
